.Tag::before,
.Button::after,
.Button::before,
[type=checkbox]::after,
[type=checkbox]::before,
[type=radio]::after,
[type=radio]::before,
a::before {
	position: absolute;
	z-index: -1;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	content: '';
	opacity: 0;
	border-radius: inherit;
	background-image: inherit;
	background-repeat: inherit;
	background-position: inherit;
}

