input,
textarea,
select {
	padding: var(--input-padding);
	transition-timing-function: ease;
	transition-duration: .2s;
	transition-property: border;
	vertical-align: middle;
	color: var(--body-color);
	border: 1px solid var(--input-border-color);
	border-radius: 3px;
	outline: none;
	background: var(--input-background);
	--input-padding: calc(var(--spacer-base) * .5) calc(var(--spacer-base) * 1);
	--input-border-color: #ccc;
	--input-hover-border-color: gray;
	--input-background: #f7f7f7;
	appearance: none;
	filter: none;
}

@media (prefers-color-scheme: dark) {
	input,
    textarea,
    select {
		--input-border-color: gray;
		--input-hover-border-color: #ccc;
		--input-background: #333;
	}
}

input:hover,
input:focus,
textarea:hover,
textarea:focus,
select:hover,
select:focus {
	border-color: var(--input-hover-border-color);
}

input:invalid:focus,
textarea:invalid:focus,
select:invalid:focus {
	border-color: #d94d4d;
}

input[disabled],
textarea[disabled],
select[disabled] {
	cursor: not-allowed;
	opacity: .5;
}

input[disabled]:hover,
input[disabled]:focus,
textarea[disabled]:hover,
textarea[disabled]:focus,
select[disabled]:hover,
select[disabled]:focus {
	border-color: var(--input-border-color);
}

select,
[type=date],
[type=time],
[type=color],
[type=file],
[type=checkbox],
[type=radio],
[type=range] {
	cursor: pointer;
}

[type=file] {
	background: transparent;
}

[type=file]:not(:focus) {
	border-color: transparent;
}

[type=color] {
	height: 35px;
	padding: calc(var(--spacer-base) * .5);
}

textarea {
	min-height: calc(1rem + calc(var(--spacer-base) * .5) * 2);
	transition-property: border;
}

select {
	--select-icon: url('data:image/svg+xml,%3csvg viewBox=\'0 0 20 20\' xmlns=\'http://www.w3.org/2000/svg\'%3e%3cpath d=\'m5 8 5 5 5-5\' fill=\'none\' stroke=\'%23000\' stroke-linecap=\'round\' stroke-linejoin=\'round\' stroke-width=\'2\'/%3e%3c/svg%3e');
}

@media (prefers-color-scheme: dark) {
	select {
		--select-icon: url('data:image/svg+xml,%3csvg viewBox=\'0 0 20 20\' xmlns=\'http://www.w3.org/2000/svg\'%3e%3cpath d=\'m5 8 5 5 5-5\' fill=\'none\' stroke=\'%23fff\' stroke-linecap=\'round\' stroke-linejoin=\'round\' stroke-width=\'2\'/%3e%3c/svg%3e');
	}
}

select:not([multiple]) {
	padding-right: 38px;
	background-image: var(--select-icon);
	background-repeat: no-repeat;
	background-position: right .6rem center;
	background-size: 20px;
}

select[multiple] {
	resize: vertical;
	scrollbar-width: initial;
	scrollbar-color: initial;
}

select[multiple] option {
	padding: calc(var(--spacer-base) * .75) calc(var(--spacer-base) * 1);
	border-top: 1px solid var(--input-border-color);
}

select[multiple] option:first-child {
	border: none;
}

[type=checkbox],
[type=radio] {
	position: relative;
	z-index: 1;
	width: 20px;
	height: 20px;
	margin: calc(var(--spacer-base) * .5) calc(var(--spacer-base) * 1);
	border-radius: 3px;
	--checkbox-checked-background: #333;
	--checkbox-checked-icon-color: #f2f2f2;
	--checkbox-checked-icon: url('data:image/svg+xml,%3csvg xmlns=\'http://www.w3.org/2000/svg\' viewBox=\'0 0 20 20\'%3e%3cpath fill=\'none\' stroke=\'%23f2f2f2\' stroke-linecap=\'round\' stroke-linejoin=\'round\' stroke-width=\'2\' d=\'M6 10l3 3l6-6\'/%3e%3c/svg%3e');
	--radio-checked-icon: url('data:image/svg+xml,%3csvg xmlns=\'http://www.w3.org/2000/svg\' viewBox=\'-4 -4 8 8\'%3e%3ccircle r=\'1.5\' fill=\'%23f2f2f2\'/%3e%3c/svg%3e');
	--checkbox-indeterminate-icon: url('data:image/svg+xml,%3csvg xmlns=\'http://www.w3.org/2000/svg\' viewBox=\'0 0 20 20\'%3e%3cpath fill=\'none\' stroke=\'%23f2f2f2\' stroke-linecap=\'round\' stroke-linejoin=\'round\' stroke-width=\'3\' d=\'M6 10h8\'/%3e%3c/svg%3e');
}

@media (prefers-color-scheme: dark) {
	[type=checkbox],
    [type=radio] {
		--checkbox-checked-background: #fff;
		--checkbox-checked-icon-color: #0d0d0d;
		--checkbox-checked-icon: url('data:image/svg+xml,%3csvg xmlns=\'http://www.w3.org/2000/svg\' viewBox=\'0 0 20 20\'%3e%3cpath fill=\'none\' stroke=\'%230d0d0d\' stroke-linecap=\'round\' stroke-linejoin=\'round\' stroke-width=\'2\' d=\'M6 10l3 3l6-6\'/%3e%3c/svg%3e');
		--radio-checked-icon: url('data:image/svg+xml,%3csvg xmlns=\'http://www.w3.org/2000/svg\' viewBox=\'-4 -4 8 8\'%3e%3ccircle r=\'1.5\' fill=\'%230d0d0d\'/%3e%3c/svg%3e');
		--checkbox-indeterminate-icon: url('data:image/svg+xml,%3csvg xmlns=\'http://www.w3.org/2000/svg\' viewBox=\'0 0 20 20\'%3e%3cpath fill=\'none\' stroke=\'%230d0d0d\' stroke-linecap=\'round\' stroke-linejoin=\'round\' stroke-width=\'2\' d=\'M6 10h8\'/%3e%3c/svg%3e');
	}
}

[type=checkbox]::after,
[type=checkbox]::before,
[type=radio]::after,
[type=radio]::before {
	transition-timing-function: ease;
	transition-duration: .2s;
	transition-property: opacity;
}

[type=checkbox]::after,
[type=radio]::after {
	background-color: var(--checkbox-checked-background);
}

[type=checkbox]::before,
[type=radio]::before {
	outline: 3px solid #666;
	outline-offset: 3px;
}

[type=checkbox]:checked,
[type=checkbox]:indeterminate,
[type=radio]:checked,
[type=radio]:indeterminate {
	--input-border-color: var(--checkbox-checked-background);
	--input-hover-border-color: var(--checkbox-checked-background);
}

[type=checkbox]:checked::after,
[type=checkbox]:indeterminate::after,
[type=radio]:checked::after,
[type=radio]:indeterminate::after {
	opacity: 1;
	border: 1px solid var(--checkbox-checked-background);
}

[type=checkbox]:focus::before,
[type=radio]:focus::before {
	opacity: 1;
}

[type=checkbox]::after {
	border-radius: 2px;
	background-image: var(--checkbox-checked-icon);
}

[type=radio] {
	border-radius: 50%;
}

[type=radio]::after {
	border-radius: 40%;
	background-image: var(--radio-checked-icon);
}

[type=checkbox]:indeterminate::after,
[type=radio]:indeterminate::after {
	background-image: var(--checkbox-indeterminate-icon);
}

.Switch {
	width: 40px;
	height: 20px;
	transition-timing-function: ease;
	transition-duration: .2s;
	transition-property: border, background;
	border-radius: 2rem;
}

.Switch::after {
	top: 0;
	bottom: 0;
	left: 0;
	width: calc(20px - (1px * 2));
	height: 100%;
	transition-timing-function: ease;
	transition-duration: .2s;
	transition-property: left, background;
	opacity: 1;
	border-radius: calc(2rem - 1px);
	background: var(--checkbox-checked-background);
}

.Switch:checked {
	background-color: var(--checkbox-checked-background);
}

.Switch:checked::after {
	left: calc(40px - 20px);
	background: var(--checkbox-checked-icon-color);
}

.Switch:indeterminate::after {
	left: 0;
	width: 100%;
