.Blockquote:before,
.Afb blockquote:before {
	font-family: georgia, serif;
	font-size: 7em;
	line-height: .9;
	position: absolute;
	top: 15px;
	left: 15px;
	content: '“';
	color: var(--blockquote-quote-color);
}

.Blockquote > cite,
.Afb blockquote > cite {
	display: block;
	margin-bottom: calc(var(--spacer-base) * .75);
	text-align: right;
	color: var(--blockquote-cite-color);
}

.Blockquote > cite:before,
.Afb blockquote > cite:before {
	content: '- ';
}

.Blockquote > :first-child,
.Afb blockquote > :first-child {
	margin-top: 0;
}

.Blockquote > :last-child,
.Afb blockquote > :last-child {
	margin-bottom: 0;
}

.Blockquote-inverted,
.Afb blockquote-inverted {
	--blockquote-background: #333;
	--blockquote-color: #f2f2f2;
	--blockquote-cite-color: #999;
	--blockquote-quote-color: #666;
}

@media (prefers-color-scheme: dark) {
	.Blockquote-inverted,
    .Afb blockquote-inverted {
		--blockquote-background: #e6e6e6;
		--blockquote-color: #0d0d0d;
		--blockquote-cite-color: #4d4d4d;
		--blockquote-quote-color: #b3b3b3;
	}
}

.Blockquote-light,
.Afb blockquote-light {
	--blockquote-background: #e6e6e6;
	--blockquote-color: #0d0d0d;
	--blockquote-cite-color: #4d4d4d;
	--blockquote-quote-color: #b3b3b3;
}

.Blockquote-dark,
.Afb blockquote-dark {
	--blockquote-background: #333;
	--blockquote-color: #f2f2f2;
	--blockquote-cite-color: #999;
	--blockquote-quote-color: #666;
}

.Figure,
.Afb figure {
	display: flex;
	flex-direction: column;
	margin-top: calc(calc(var(--spacer-base) * .75) * 2);
	margin-bottom: calc(calc(var(--spacer-base) * .75) * 2);
	padding: calc(var(--spacer-base) * 2.5);
	border-radius: 12px;
	background: #333;
	align-items: center;
	justify-content: center;
}

.Figure figcaption,
.Afb figure figcaption {
	margin-top: calc(var(--spacer-base) * 2.5);
	text-align: center;
	color: #d9d9d9;
}

.Figure img,
.Afb figure img {
	max-height: 60vh;
}

.Figure--light {
	background: #e6e6e6;
}

.Figure--light figcaption {
	color: #404040;
}
